<template>
    <div class="container back-one">
        <div class="capital-search-page">
            <ul>
                <li>
                    <span>开始时间</span>
                    <div class="fr" @click="show2=true">
                        <span>{{config.startTime}}</span>
                    </div>
                </li>
                <li>
                    <span>结束时间</span>
                    <div class="fr" @click="show3=true">
                        <span>{{config.endTime}}</span>
                    </div>
                </li>
                <li>
                    <span>类型</span>
                    <div class="fr" @click="show1=true">
                        <span v-if="columns1.length > 0">{{columns1[index]}}</span>
                        <span v-else><img src="../../static/img/load.gif" width="20" alt=""></span>
                    </div>
                </li>
            </ul>
        </div>
        <div class="fixed-btn">
            <button @click="search">查询</button>
        </div>

        <van-popup v-model="show1" position="bottom" :style="{ height: '40%' }">
            <van-picker
                title="类型"
                show-toolbar
                :columns="columns1"
                @confirm="onConfirm1"
                @cancel="show1=false"
                />
        </van-popup>
        <van-popup v-model="show2" position="bottom" :style="{ height: '40%' }">
            <van-datetime-picker
            v-model="currentDate1"
            type="date"
            title="选择年月日"
            @confirm="onConfirm2"
            @cancel="show2=false"
            />
        </van-popup>
        <van-popup v-model="show3" position="bottom" :style="{ height: '40%' }">
            <van-datetime-picker
            v-model="currentDate2"
            type="date"
            title="选择年月日"
            @confirm="onConfirm3"
            @cancel="show3=false"
            />
        </van-popup>
    </div>
</template>

<script>
    import { changeTimeType, getToday, getTomorrow } from "@/tool/common/time.js";
    import { mapState } from "vuex";
    export default {
        data(){
            return {
                show1: false,
                index: 0,

                show2:false,
                currentDate1: new Date(),

                show3:false,
                currentDate2: new Date(),
                
                config: {
                    startTime: '',
                    endTime: '',
                    type: '',
                }
            }
        },
        watch:{
            index(){
                this.config.type = this.entityList[this.index].id
            }
        },
        computed:{
            ...mapState('User', {
                entityList: function(state){
                    if(state.getCoinOperEnumList){
                        return state.getCoinOperEnumList.entityList
                    }else{
                        this.getType()
                        return state.getCoinOperEnumList
                    }
                }
            }),
            columns1(){
                var arr = ['全部']
                if(this.entityList){
                    this.entityList.forEach(item =>{
                        arr.push(item.name)
                    })
                }
                return arr
            },
            
        },
        created(){
            
            this.config.startTime = getToday()
            this.config.endTime = getTomorrow()
        },
        methods: {
            search(){
                this.$store.commit('User/SET_CAPIAL_RECORD', this.config)
                this.$router.go(-1);
            },
            onConfirm1(e, index){
                this.show1 = false
                this.index = index
            },
            onConfirm2(e){
                this.show2 = false
                this.currentDate1 = e
                this.config.startTime = changeTimeType(e)
            },
            onConfirm3(e){
                this.show3 = false
                this.currentDate2 = e
                this.config.endTime = changeTimeType(e)
            },
            getType(){
                this.$store.dispatch('User/getCoinOperEnumList')
            }
        }
    }
</script>

